.item {
    display: flex;
    flex-direction: row;
    gap:20px;
    height: 70px;
    align-items: center;
}
.avatar{
    height: 50px;
    width: 50px;
}
.name {
    font-size: 20px;
}
.status {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: red;
}
.active {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: green
}  
